#name {
    color: #4D2601;
    text-align: center;
    font-family: 'Grand Wilson Sans';
    font-size: 95px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
  
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
  
    user-select: none;
}
  
#name-w {
    user-select: none;
    letter-spacing: -25px;
}

#bottom-border {
    position: fixed;
    bottom: 0px;
    height: 140px;
    -webkit-filter: drop-shadow(0px 1px 6px rgba(0, 0, 0, 0.2));
    filter: drop-shadow(0px 1px 6px rgba(0, 0, 0, 0.2));  
}

.background-image {
    height: 100%;
    width: 100%;
    object-fit: contain;
  
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-52%);
    object-position: center 75%;
}


.triangles {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    content-visibility: auto;
}
  
@keyframes animate {
    0% {
        opacity: 1;
        border-radius: 0;
        transform: translateY(0) rotate(0deg);
    }
    100% {
        opacity: 0;
        border-radius: 50%;
        transform: translateY(-1000px) rotate(720deg);
    }
}
  
.triangles div {
    position: absolute;
    display: block;
    width: 20px;
    height: 20px;
    background: #E7E7D7;
    bottom: -150px;
    animation: animate 25s linear infinite;
}
  
.triangles div:nth-child(1) {
    left: 25%;
    width: 80px;
    height: 80px;
    animation-delay: 0s;
}

.triangles div:nth-child(2) {
    left: 10%;
    width: 60px;
    height: 60px;
    animation-delay: 2s;
    animation-duration: 12s;
}

.triangles div:nth-child(3) {
    left: 70%;
    width: 20px;
    height: 20px;
    animation-delay: 4s;
}

.triangles div:nth-child(4) {
    left: 40%;
    width: 60px;
    height: 60px;
    animation-delay: 0s;
    animation-duration: 18s;
}

.triangles div:nth-child(5) {
    left: 65%;
    width: 20px;
    height: 20px;
    animation-delay: 0s;
}

.triangles div:nth-child(6) {
    left: 75%;
    width: 80px;
    height: 80px;
    animation-delay: 3s;
}

.triangles div:nth-child(7) {
    left: 35%;
    width: 150px;
    height: 150px;
    animation-delay: 7s;
}

.triangles div:nth-child(8) {
    left: 50%;
    width: 25px;
    height: 25px;
    animation-delay: 15s;
    animation-duration: 45s;
}

.triangles div:nth-child(9) {
    left: 20%;
    width: 15px;
    height: 15px;
    animation-delay: 2s;
    animation-duration: 35s;
}

.triangles div:nth-child(10) {
    left: 85%;
    width: 150px;
    height: 150px;
    animation-delay: 0s;
    animation-duration: 11s;
}

@media (max-width: 800px) {
    #name {
        font-size: 80px;
    }
    
    #name-w {
    letter-spacing: -20px;
    }
    
    .background-image {
    width: 120%;
    }
    
    .triangles div:nth-child(4),
    .triangles div:nth-child(5),
    .triangles div:nth-child(7),
    .triangles div:nth-child(8),
    .triangles div:nth-child(10) {
    display: none;
    }
}